import { Component } from "@angular/core";
import {Http, Response} from '@angular/http';

@Component({
  selector: "page-sub",
  template: `
    <div *ngIf="loading">{{loading}}loading...</div>
    <div class="weui_panel_bd">
      <a  *ngFor="let item of list" href="javascript:void(0);" class="weui_media_box weui_media_appmsg">
                <div class="weui_media_hd">
                    <img class="weui_media_appmsg_thumb" src="data:image/png;base64," alt="">
                </div>
                <div class="weui_media_bd">
                    <h4 class="weui_media_title">标题一</h4>
                    <p class="weui_media_desc">由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。</p>
                </div>
            </a>
    </div>
  `
})
export class PageComponent {
    data: Object;
    loading: boolean;
    list:Object;
    constructor(public http: Http) {
        this.makeRequest();
    }
    makeRequest(): void {
        this.loading = true;
        this.http.get('data.json')
            .subscribe((res: Response) => {
                console.log(res);
                this.list = [{"title":"xxxx"},{"title":"xxxxx1111"},{"title":"sdfsafasfsaf"}];
                this.loading = false;
            });
    }
}



